.main {
    text-align: center;
    padding-top: 120px;

}

.title {
    font-size: 64px;
    line-height: 75px;
    font-style: normal;
    font-weight: normal;
}

.desc {
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    line-height: 140%;
    max-width: 268px;
    margin: 26px auto;
}

.desc::after {
    content: "";
    display: block;
    margin: auto;
    margin-top: 26px;
    width: 1px;
    height: 60px;
    background-color: #ffffff;
}